.wrap {
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrap .head {
  width: 90%;
  padding: 50px;
}
.wrap .head .left {
  float: left;
  margin-right: 50px;
  width: 10%;
}
.wrap .head .right {
  width: 80%;
  margin-top: 30px;
  float: left;
  font-weight: 600;
  font-size: 28px;
  overflow: hidden;
  height: 80px;
}
.wrap .head .right ul {
  text-align: left;
  list-style: none;
  animation: 50s linear 0s normal none infinite change;
  -webkit-animation: 50s linear 0s normal none infinite change;
  -moz-animation: 50s linear 0s normal none infinite change;
  -o-animation: 50s linear 0s normal none infinite change;
}
.wrap .head .right ul li {
  line-height: 80px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
