.wrap .middle {
  width: 70%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.wrap .middle .box {
  width: 28%;
  height: 150px;
  background: #c3c3cc;
  border-radius: 10px;
  margin-bottom: 50px;
  padding: 10px;
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.03);
  transition: transform 0.5s;
}
.wrap .middle .box .up {
  height: 70%;
  padding-right: 50px;
}
.wrap .middle .box .down img {
  width: 10%;
  margin: -8px 10px;
}
.wrap .middle .box .down:hover {
  cursor: pointer;
}
.wrap .middle .box:hover {
  transform: scale(1.1);
}
.wrap .footer {
  display: flex;
  width: 80%;
  justify-content: space-around;
  padding: 20px 100px;
}
.wrap .footer p {
  color: #000;
  font-size: 18px;
  font-weight: 600;
}
.wrap .footer ul {
  list-style: none;
}
.wrap .footer ul a {
  color: #6c757d;
  font-size: 14px;
}
.wrap .backTop {
  position: fixed;
  bottom: 80px;
  right: -80px;
}
.wrap .backTop img {
  width: 20%;
}
.wrap .backTop:hover {
  cursor: pointer;
}
